<template>
  <div id="app">
    <div>
      <el-container>
        <el-header style="height: 140px; border-bottom: 1px solid #eee">
          <div
            class="header-image"
            :style="{ 'padding-left': logoImagePosition }"
          >
            <el-image style="height: 80px;" :src="logoImageSrc"></el-image>
          </div>
          <div
            class="menuControl"
            @mouseenter="changebgcolor"
            @mouseleave="clearcolor"
            @click="collapseStatus"
            :style="{ 'background-color': collapsebgcolor }"
          >
            <i class="el-icon-s-fold" v-show="block"></i>
            <i class="el-icon-s-unfold" v-show="toggle"></i>
          </div>
          <div style="float:right;margin-top: -15px;">
            <el-select
              class="languageClass"
              v-model="languageValue"
              @change="changeLanguage"
            >
              <el-option
                style="font-size: smaller;"
                v-for="item in languageOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
        </el-header>
        <el-container>
          <el-aside class="aside-menu" :width="collapseSize">
            <el-menu
              router
              default-active="$router.currentRoute.path"
              :default-active="currentMenu"
              class="el-menu-vertical-demo"
              @select="handleSelect"
              @open="handleOpen"
              @close="handleClose"
              :collapse="isCollapse"
            >
              <el-menu-item :index="homeRoute">
                <i class="el-icon-s-home"></i>
                <span class="home-class" slot="title">Home</span>
              </el-menu-item>

              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-coin"></i>
                  <span class="el-menu-span1" slot="title">SQL</span>
                </template>
                <el-submenu index="1-1">
                  <span class="el-menu-span2" slot="title">{{
                    $t("navMenu.webType")
                  }}</span>
                  <el-menu-item :index="webHttpRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.webHttp") }}
                    </span>
                  </el-menu-item>
                  <el-menu-item :index="webHandlerRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.wehHandler") }}
                    </span>
                  </el-menu-item>
                </el-submenu>
                <el-submenu index="1-2">
                  <span class="el-menu-span2" slot="title">{{
                    $t("navMenu.microType")
                  }}</span>
                  <el-menu-item :index="microRPCRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.microRpc") }}
                    </span>
                  </el-menu-item>
                  <el-menu-item :index="microServiceRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.microService") }}
                    </span>
                  </el-menu-item>
                  <el-menu-item :index="microProtobufRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.microProtobuf") }}
                    </span>
                  </el-menu-item>
                </el-submenu>
              </el-submenu>

              <el-submenu index="2">
                <template slot="title">
                  <i class="el-icon-tickets"></i>
                  <span class="el-menu-span1" slot="title">Protobuf</span>
                </template>
                <el-submenu index="2-1">
                  <span class="el-menu-span2" slot="title">{{
                    $t("navMenu.webType")
                  }}</span>
                  <el-menu-item :index="webHTTPPbRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.webHttpPb") }}
                    </span>
                  </el-menu-item>
                  <el-menu-item :index="microRPCGwPbRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.microRpcGwPb") }}
                    </span>
                  </el-menu-item>
                </el-submenu>
                <el-submenu index="2-2">
                  <span class="el-menu-span2" slot="title">{{
                    $t("navMenu.microType")
                  }}</span>
                  <el-menu-item :index="microRPCPbRoute">
                    <span class="el-menu-span3"
                      >{{ $t("navMenu.microrpcPb") }}
                    </span>
                  </el-menu-item>
                </el-submenu>
              </el-submenu>

              <el-submenu index="3">
                <template slot="title">
                  <i class="el-icon-suitcase"></i>
                  <span class="el-menu-span1" slot="title">Public</span>
                </template>
                <el-menu-item :index="webDaoRoute">
                  <span class="el-menu-span3">{{ $t("navMenu.webDao") }} </span>
                </el-menu-item>
                <el-menu-item :index="webModelRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.webModel") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="yamlConfigRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.yamlConfig") }}
                  </span>
                </el-menu-item>
                <el-menu-item :index="microRPCClientRoute">
                  <span class="el-menu-span3"
                    >{{ $t("navMenu.microRpcCli") }}
                  </span>
                </el-menu-item>
              </el-submenu>

              <!-- <el-submenu
                :index="item.id"
                v-for="(item, index) in items"
                :key="index"
              >
                <template slot="title">
                  <i class="el-icon-suitcase"></i>
                  <span class="el-menu-span1" slot="title">{{
                    item.name
                  }}</span>
                </template>
                <el-submenu
                  :index="sub.id"
                  v-for="(sub, index) in item.subitem"
                  :key="index"
                >
                  <span class="el-menu-span2" slot="title">{{ sub.name }}</span>
                  <el-menu-item
                    :index="sub.id"
                    v-for="(sub, index) in sub.subitem"
                    :key="index"
                    @click="clickHandle(sub)"
                  >
                    <span class="el-menu-span3">{{ sub.name }} </span>
                  </el-menu-item>
                </el-submenu>
              </el-submenu> -->
            </el-menu>
          </el-aside>

          <el-container>
            <el-main>
              <!-- 路由 -->
              <router-view />
            </el-main>
            <el-footer>
              <div style="text-align: center; position:relative;">
                <p style="font-style:italic; font-size: small;color: #666; ">
                  {{ $t("footer.text1") }}
                  <a href="https://github.com/zhufuyi/sponge" target="_blank">
                    star⭐</a
                  >
                  {{ $t("footer.text2") }}
                  <el-button
                    type="text"
                    style="font-size: small;"
                    @click="dialogTableVisible = true"
                    >{{ $t("footer.text3") }}</el-button
                  >
                </p>

                <el-dialog
                  title="加微信进群"
                  :visible.sync="dialogTableVisible"
                >
                  <el-image
                    style="height: 320px;"
                    :src="wechatImageSrc"
                  ></el-image>
                </el-dialog>
              </div>
            </el-footer>
          </el-container>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      isCollapse: false,
      // collapseBtnClick: false,
      collapseSize: "320px",
      toggle: false,
      block: true,
      collapsebgcolor: "",

      dialogTableVisible: false,
      logoImageSrc: require("@/assets/go-sponge-logo.png"),
      logoImagePosition: "15%",
      wechatImageSrc: require("@/assets/wechat.jpg"),
      currentMenu: "/ui",
      items: [
        {
          name: "SQL",
          id: "1",
          subitem: [
            {
              // name: this.$t("content.myText"),
              name: "Web类型",
              id: "1-1",
              subitem: [
                {
                  name: "创建web项目",
                  id: "/ui/web-http",
                },
                {
                  name: "生成handler CRUD代码",
                  id: "/ui/web-handler",
                },
              ],
            },
            {
              name: "RPC类型",
              id: "1-2",
              subitem: [
                {
                  name: "创建rpc项目",
                  id: "/ui/micro-rpc",
                },
                {
                  name: "生成service CRUD代码",
                  id: "/ui/micro-service",
                },
                {
                  name: "生成protobuf描述信息",
                  id: "/ui/micro-protobuf",
                },
              ],
            },
          ],
        },
        {
          name: "Protobuf",
          id: "2",
          subitem: [
            {
              name: "Web类型",
              id: "2-1",
              subitem: [
                {
                  name: "创建web项目",
                  id: "/ui/web-http-pb",
                },
                {
                  name: "创建rpc gateway项目",
                  id: "/ui/micro-rpc-gw-pb",
                },
              ],
            },
            {
              name: "RPC类型",
              id: "2-2",
              subitem: [
                {
                  name: "创建rpc项目",
                  id: "/ui/micro-rpc-pb",
                },
              ],
            },
          ],
        },
        {
          name: "Public",
          id: "3",
          subitem: [
            {
              name: "Yaml",
              id: "3-1",
              subitem: [
                {
                  name: "生成config代码",
                  id: "/ui/yaml-config",
                },
              ],
            },
            {
              name: "Dao",
              id: "3-2",
              subitem: [
                {
                  name: "生成dao CRUD代码",
                  id: "/ui/web-dao",
                },
              ],
            },
            {
              name: "Model",
              id: "3-3",
              subitem: [
                {
                  name: "生成model代码",
                  id: "/ui/web-model",
                },
              ],
            },
            {
              name: "RPC client",
              id: "3-4",
              subitem: [
                {
                  name: "生成rpc client代码",
                  id: "/ui/micro-rpc-cli",
                },
              ],
            },
          ],
        },
      ],

      languageOptions: [
        {
          value: "English",
          label: "English",
        },
        {
          value: "中文",
          label: "中文",
        },
      ],
      languageValue: "中文",

      homeRoute: "/ui",
      webHttpRoute: "/ui/web-http",
      webHandlerRoute: "/ui/web-handler",
      webDaoRoute: "/ui/web-dao",
      webModelRoute: "/ui/web-model",
      microRPCRoute: "/ui/micro-rpc",
      microServiceRoute: "/ui/micro-service",
      microRPCClientRoute: "/ui/micro-rpc-cli",
      microRPCPbRoute: "/ui/micro-rpc-pb",
      microRPCGwPbRoute: "/ui/micro-rpc-gw-pb",
      microProtobufRoute: "/ui/micro-protobuf",
      webHTTPPbRoute: "/ui/web-http-pb",
      yamlConfigRoute: "/ui/yaml-config",
      downloadCodeRoute: "/ui/download",
    };
  },

  // 刷新展开折叠菜单
  watch: {
    $route(e) {
      this.currentMenu = e.path;
    },
  },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  created() {
    let localeLanguage = localStorage.getItem("localeLanguage");
    if (localeLanguage == "en") {
      this.$i18n.locale = "en";
      this.languageValue = "English";
    } else {
      this.$i18n.locale = "cn";
      this.languageValue = "中文";
    }
    this.adaptedRouteByLanguage();

    this.currentMenu = this.$route.path;
    window.addEventListener("resize", this.handleResize);
  },

  beforeDestroy: function() {
    window.removeEventListener("resize", this.handleResize);
  },

  methods: {
    handleResize(event) {
      this.fullWidth = document.documentElement.clientWidth;
    },
    handleSelect(key, keyPath) {
      // this.currentMenu = key;
    },
    handleOpen(key, keyPath) {
      // this.currentMenu = key;
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // this.currentMenu = key;
      // console.log(key, keyPath);
    },
    clickHandle(sub) {
      // console.log(sub.id, sub.name);
    },

    collapseStatus() {
      // this.collapseBtnClick = this.isCollapse;
      this.toggle = !this.toggle;
      this.block = !this.block;
      this.isCollapse = !this.isCollapse;
      if (this.isCollapse) {
        this.collapseSize = "0px";
        this.logoImagePosition = "0%";
      } else {
        this.collapseSize = "320px";
        this.logoImagePosition = "15%";
      }
    },

    // 鼠标移动上更换背景色
    changebgcolor() {
      // index:鼠标移上的元素的索引值
      this.collapsebgcolor = "#66B1FF";
    },
    //鼠标移出，还原颜色
    clearcolor() {
      this.collapsebgcolor = "";
    },

    changeLanguage(value) {
      this.languageValue = value;
      if (value === "中文") {
        this.$i18n.locale = "cn";
        localStorage.setItem("localeLanguage", "cn");
      } else {
        this.$i18n.locale = "en";
        localStorage.setItem("localeLanguage", "en");
      }
      this.adaptedRouteByLanguage();

      this.$router.push(this.homeRoute);
      window.location.reload();
    },

    adaptedRouteByLanguage() {
      var enPrefix = "";
      if (this.$i18n.locale == "en") {
        enPrefix = "/en";
      }

      this.homeRoute = enPrefix + this.homeRoute.replace("/en", "");
      this.webHttpRoute = enPrefix + this.webHttpRoute.replace("/en", "");
      this.webHandlerRoute = enPrefix + this.webHandlerRoute.replace("/en", "");
      this.webDaoRoute = enPrefix + this.webDaoRoute.replace("/en", "");
      this.webModelRoute = enPrefix + this.webModelRoute.replace("/en", "");
      this.microRPCRoute = enPrefix + this.microRPCRoute.replace("/en", "");
      this.microRPCClientRoute =
        enPrefix + this.microRPCClientRoute.replace("/en", "");
      this.microRPCPbRoute = enPrefix + this.microRPCPbRoute.replace("/en", "");
      this.microRPCGwPbRoute =
        enPrefix + this.microRPCGwPbRoute.replace("/en", "");
      this.microServiceRoute =
        enPrefix + this.microServiceRoute.replace("/en", "");
      this.microProtobufRoute =
        enPrefix + this.microProtobufRoute.replace("/en", "");
      this.webHTTPPbRoute = enPrefix + this.webHTTPPbRoute.replace("/en", "");
      this.yamlConfigRoute = enPrefix + this.yamlConfigRoute.replace("/en", "");
      this.downloadCodeRoute =
        enPrefix + this.downloadCodeRoute.replace("/en", "");
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
        this.logoImagePosition = "0%";
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
        this.logoImagePosition = "15%";
      }
    },
  },
};
</script>

<style scoped>
#app {
  margin: 0px;
  padding: 0px;
  background: url("assets/body-background.png") repeat;
}

.menuControl {
  cursor: pointer;
  float: left;
  width: 50px;
  height: 32px;
  border-radius: 10%;
  margin-top: -10px;
}

.aside-menu {
  margin: 0px;
  padding: 0px;
  background-color: #f8f8f8;
}

.aside-menu /deep/ .el-menu {
  background-color: #f8f8f8;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  /* width: 280px; */
  min-height: 880px;
}
.el-submenu .el-menu-item {
  font-family: "Microsoft YaHei UI";
  padding: 0 15px;
}

.el-col {
  /* border-radius: 4px; */
  background: #99a9bf;
  background: url("assets/body-background.png");
}

.home-class {
  font-weight: bold;
  font-size: 15px;
}

.el-menu-span1 {
  font-family: "Microsoft YaHei UI";
  font-weight: bold;
  font-size: 15px;
}
.el-menu-span2 {
  font-family: "Microsoft YaHei UI";
  font-weight: bold;
  padding: 0 15px;
  font-size: 14px;
}
.el-menu-span3 {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  padding: 0 15px;
}

.header-image {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  padding-top: 20px;
  padding-left: 15%;
}

.languageClass /deep/ .el-input__inner {
  width: 120px;
  font-size: small;
  background-color: transparent;
  color: #409eff;
  border: none;
}
</style>



