<template>
  <el-row :gutter="20">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 50px;">
        <a href="https://github.com/zhufuyi/sponge" target="_blank">sponge</a>
        is a powerful tool for generating code for web services and microservices projects, as well as a microservices framework based on gin and grpc encapsulation. Sponge has a wealth of code generation commands for generating web services, rpc services, and rpc gateway service code. With its decoupled modular design, it is easy to build complete project code from development to deployment, making it more convenient, easy, and efficient to develop with the go language.
      </p>
      <p>
        github:
        <a href="https://github.com/zhufuyi/sponge" target="_blank"
          >https://github.com/zhufuyi/sponge</a
        >
      </p>
      <p>
        documentation:
        <a href="https://go-sponge.com" target="_blank"
          >https://go-sponge.com</a
        >
      </p>
      <br />
      <p>sponge generates code framework diagrams.</p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>Microservice service framework diagram.</p>
      <div class="microservices-image">
        <el-image :src="mircoServiceFrameworkImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      mircoServiceFrameworkImageSrc: require("@/assets/microservices-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.microservices-image {
  width: 70%;
  margin: auto;
}
</style>



