<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="mysql dsn地址" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/dbName"
            ></el-input>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              >获取表名</el-link
            >
          </el-form-item>
          <el-form-item label="表名" prop="tableName">
            <el-select
              style="width: 80%; font-size: small"
              v-model="ruleForm.tableName"
              placeholder="请选择表名，支持多选"
              clearable
              filterable
              multiple
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item label="嵌入Model">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                生成model代码的表结构体是否嵌入gorm.Model，<br />
                如果不嵌入gorm.Model，表的列名和struct字段一一对应。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="初始化db">
            <el-switch v-model="ruleForm.includeInitDB"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                选中这个选项生成的dao代码包括了mysql和redis初始化代码，<br />
                通常用在基于protobuf创建的web或rpc的项目中，如果是<br />
                基于mysql表生成的web或rpc项目则不需要选中。
              </div>
            </el-tooltip>
          </el-form-item>
          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "WebModel",
  data() {
    return {
      title: "生成Model代码",
      path: "web-model",
      helpInfo: `
      <p>基于mysql表生成对应gorm的结构体。</p>
      `,
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      ruleForm: {
        dsnAddr: "",
        tableName: [],
        embed: true,
      },
      rules: {
        dsnAddr: [
          {
            required: true,
            message: "请输入连接mysql dsn地址",
            trigger: "blur",
          },
        ],
        tableName: [
          { required: true, message: "请选择表名", trigger: "change" },
        ],
      },

      tableNames: [],
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;

        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          // this.ruleForm.tableName = data.data.tableName;
          this.ruleForm.embed = data.data.embed;

          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          var selectTableNames = this.ruleForm.tableName.join(",");
          const arg = `web model --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --embed=${this.ruleForm.embed}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = {
        dsnAddr: this.ruleForm.dsnAddr,
        embed: true,
      };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
      };
      axios
        .post(apiUrl, formdata, { timeout: 5000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("获取表名成功")
              : this.$message.error("获取表名失败");
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          this.$message.error("获取表名失败");
          // console.log(err);
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: small;
}
</style>



