<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="proto文件" prop="protobufFile">
            <el-link
              style="margin-left: 10%;padding-bottom: 5px; font-size: 16px;"
              type="primary"
              @click="showProtoFileDemo"
              >查看proto文件示例</el-link
            >

            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".proto"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>点击选择proto文件</em><br />或将文件拖到此处，支持多文件
              </div>
              <!-- <div class="el-upload__tip" slot="tip">
                只能上传proto文件
              </div> -->
            </el-upload>
          </el-form-item>

          <el-form-item
            label="服务名称"
            prop="serverName"
            style="margin-top: 50px;"
          >
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.serverName"
            ></el-input>
          </el-form-item>
          <el-form-item label="module名称" prop="moduleName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                go.mod文件的module名称。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="项目名称" prop="projectName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.projectName"
            ></el-input>
          </el-form-item>
          <el-form-item label="镜像仓库地址">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                镜像仓库地址，默认值是image-repo-host，这个参数用在构建docker镜像、<br />
                k8s的部署脚本上，如果使用docker官方镜像仓库，只需填写注册docker仓库的用户名，<br />
                如果使用私有仓库地址，需要填写完整仓库地址。
              </div>
            </el-tooltip>
          </el-form-item>
          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>

      <el-dialog
        title="proto文件示例"
        :visible.sync="showProtoDemoDialogVisible"
        width="66%"
        center
      >
        <div class="showProtoDemoDiv">
          <pre>{{ protoDemoContent }}</pre>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "MicroRPCGwPb",
  data() {
    return {
      title: "创建rpc网关项目，生成rpc网关服务代码",
      path: "micro-rpc-gw-pb",
      helpInfo: `
      <p>基于protobuf创建的rpc gateway项目包括了开发到部署所需要的功能。rpc方法转换为http接口，在http接口里面调用rpc方法。</p>
      <br />
      <p>1. 解压文件。</p>
      <p>2. 打开终端，执行命令生成*pb.go文件、生成service模板代码、更新swagger文档：<b style="color: green">make proto</b></p>
      <p>3. 打开文件 <span style="color: chocolate">internal/service/xxx_logic.go</span>, 根据模板代码示例填充代码替换 <span style="color: chocolate">panic("implement me")</span></p>
      <p>4. 编译和启动服务：<b style="color: green">make run</b></p>
      <p>5. 复制 <a href="http://localhost:8080/apis/swagger/index.html" target="_blank">http://localhost:8080/apis/swagger/index.html</a> 到浏览器测试api接口。</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      fileList: [],
      ruleForm: {
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        protobufFile: "",
      },
      rules: {
        moduleName: [
          { required: true, message: "请输入go module名称", trigger: "blur" },
        ],
        serverName: [
          { required: true, message: "请输入服务名称", trigger: "blur" },
        ],
        projectName: [
          { required: true, message: "请输入项目名称", trigger: "blur" },
        ],
        protobufFile: [
          { required: true, message: "请选择proto文件", trigger: "change" },
        ],
      },
      showProtoDemoDialogVisible: false,
      protoDemoContent: `syntax = "proto3";

package api.edusys.v1;

import "google/api/annotations.proto";
import "protoc-gen-openapiv2/options/annotations.proto";

option go_package = "edusys/api/user/v1;v1";

option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_swagger) = {
  host: "localhost:8080"
  base_path: ""
  info: {
    title: "user api docs";
    version: "v0.0.0";
  };
  schemes: HTTP;
  schemes: HTTPS;
  consumes: "application/json";
  produces: "application/json";
};

service user {
  rpc Login(LoginRequest) returns (LoginReply) {
    option (google.api.http) = {
      post: "/api/v1/login"
      body: "*"
    };
    option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_operation) = {
      summary: "登录",
      description: "登录",
      tags: "user",
    };
  }
}

message LoginRequest {
  string email = 1;
  string password = 2;
}

message LoginReply {
  string token = 1;
}`,
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.protobufFile = "";
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 上传文件
          let fd = new FormData();
          // fd.append("param", "param value");
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                // 下载代码
                const apiUrl = appConfig.spongeServiceAddr + "/generate";
                const arg = `micro rpc-gw-pb --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --protobuf-file=${res.data.data}`;
                this.$refs.downloadCode.execution(
                  apiUrl,
                  this.path,
                  arg,
                  this.helpInfo,
                );
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("下载代码失败");
            });
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = { dsnAddr: this.ruleForm.dsnAddr };
    },
    handleOnchange(file, fileList) {
      this.ruleForm.protobufFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleOnRemove(file, fileList) {
      if (fileList.length === 0) {
        this.ruleForm.protobufFile = "";
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },

    showProtoFileDemo() {
      this.showProtoDemoDialogVisible = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 60%;
  width: 150%;
}

.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  line-height: 1.5;
  margin-bottom: 20px;
}

.showProtoDemoDiv {
  width: 85%;
  margin: auto;
  font-size: 16px;
  color: black;
  background: rgba(255, 255, 255, 0.5);
  line-height: 20px;
  border: 1px solid #ccc;
  padding: 5px 20px;
  margin-bottom: 20px;
}
</style>



