<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="mysql dsn地址" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/dbName"
            ></el-input>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              >获取表名</el-link
            >
          </el-form-item>
          <el-form-item label="表名" prop="tableName">
            <el-select
              style="width: 80%; font-size: small"
              v-model="ruleForm.tableName"
              placeholder="请选择表名"
              clearable
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                创建服务只支持单表，后面需要添加新API方法，<br />在<b
                  >【生成service CRUD代码】</b
                >菜单中支持多表批量生成代码。
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="服务名称" prop="serverName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.serverName"
            ></el-input>
          </el-form-item>
          <el-form-item label="module名称" prop="moduleName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                go.mod文件的module名称。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="项目名称" prop="projectName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.projectName"
            ></el-input>
          </el-form-item>
          <el-form-item label="镜像仓库地址">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                镜像仓库地址，默认值是image-repo-host，这个参数用在构建docker镜像、<br />
                k8s的部署脚本上，如果使用docker官方镜像仓库，只需填写注册docker仓库<br />
                的用户名，如果使用私有仓库地址，需要填写完整仓库地址。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="嵌入Model">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                生成model代码的表结构体是否嵌入gorm.Model，如果<br />
                不嵌入gorm.Model，表的列名和struct字段一一对应。
              </div>
            </el-tooltip>
          </el-form-item>
          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "MicroRPC",
  data() {
    return {
      title: "创建rpc项目，生成rpc服务完整项目代码",
      path: "micro-rpc",
      helpInfo: `
      <p>基于mysql表创建的rpc项目包括了开发到部署所需的功能，包括了mysql表的增删改查方法和测试代码。</p>
      <br />
      <p>1. 解压文件。</p>
      <p>2. 打开终端，执行命令生成*.pb.go文件:  <b style="color: green">make proto</b></p>
      <p>3. 编译和启动服务:  <b style="color: green">make run</b></p>
      <p>4. 使用 <b>Goland</b> 或 <b>VS Code</b> 打开文件 <span style="color: chocolate">internal/service/xxx_client_test.go</span>，测试或者压测rpc的<b>增删改查</b>方法。</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      ruleForm: {
        dsnAddr: "",
        tableName: "",
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        embed: true,
      },
      rules: {
        dsnAddr: [
          {
            required: true,
            message: "请输入连接mysql dsn地址",
            trigger: "blur",
          },
        ],
        tableName: [
          { required: true, message: "请选择表名", trigger: "change" },
        ],
        moduleName: [
          { required: true, message: "请输入go module名称", trigger: "blur" },
        ],
        serverName: [
          { required: true, message: "请输入服务名称", trigger: "blur" },
        ],
        projectName: [
          { required: true, message: "请输入项目名称", trigger: "blur" },
        ],
      },

      tableNames: [],
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          // this.ruleForm.tableName = data.data.tableName;
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.embed = data.data.embed;

          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          const arg = `micro rpc --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${this.ruleForm.tableName} --embed=${this.ruleForm.embed}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = {
        dsnAddr: this.ruleForm.dsnAddr,
        embed: true,
      };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
      };
      axios
        .post(apiUrl, formdata, { timeout: 5000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("获取表名成功")
              : this.$message.error("获取表名失败");
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          // console.log(err);
          this.$message.error("获取表名失败");
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: small;
}
</style>



