<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="mysql dsn" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/dbName"
            ></el-input>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              >get tables</el-link
            >
          </el-form-item>
          <el-form-item label="table name" prop="tableName">
            <el-select
              style="width: 80%; font-size: small"
              v-model="ruleForm.tableName"
              placeholder="please select a table name"
              clearable
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                Creating a server only supports a single table, and new API<br />
                methods need to be added later, Multi-table batch code<br />
                generation is supported in the menu<br />
                <b>【generate service CRUD code】</b>.
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="service name" prop="serverName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.serverName"
            ></el-input>
          </el-form-item>
          <el-form-item label="module name" prop="moduleName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                module name for go.mod file.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="project name" prop="projectName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.projectName"
            ></el-input>
          </el-form-item>
          <el-form-item label="docker repository address">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                Image repository address, the default value is
                image-repo-host,<br />
                this parameter is used to build docker images, k8s deployment<br />
                scripts, if you use the official docker image repository,
                just<br />
                fill in the user name of the registered docker repository, if<br />
                you use the private repository address, you need to fill in
                the<br />
                full repository address.
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="embed model">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                Whether the table structure generating the mod code is
                embedded<br />
                in gorm.Model or not, if not, the column names and struct
                fields<br />
                of the table correspond one-to-one.
              </div>
            </el-tooltip>
          </el-form-item>
          <div style="margin: 30px auto;"></div>
        </el-form>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">Reset</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >Download code</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/en/DownloadCode";
export default {
  name: "MicroRPC",
  data() {
    return {
      title:
        "Create rpc project, generate complete project code for rpc services",
      path: "micro-rpc",
      helpInfo: `
      <p>The rpc project created based on mysql tables includes the functionality required for development to deployment, including methods for adding, deleting and checking mysql tables and testing code.</p>
      <br />
      <p>1. unzip the code file.</p>
      <p>2. open a terminal and execute the command to generate the *.pb.go file:  <b style="color: green">make proto</b></p>
      <p>3. compiling and starting services:   <b style="color: green">make run</b></p>
      <p>4. open the file using <b>Goland</b> or <b>VS Code</b> <span style="color: chocolate">internal/service/xxx_client_test.go</span>, test rpc's <b>CRUD</b> method.</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      ruleForm: {
        dsnAddr: "",
        tableName: "",
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        embed: true,
      },
      rules: {
        dsnAddr: [
          {
            required: true,
            message: "please type the mysql dsn address",
            trigger: "blur",
          },
        ],
        tableName: [
          {
            required: true,
            message: "please select a table name",
            trigger: "change",
          },
        ],
        moduleName: [
          {
            required: true,
            message: "please type the name of the go module",
            trigger: "blur",
          },
        ],
        serverName: [
          {
            required: true,
            message: "please type the name of the service",
            trigger: "blur",
          },
        ],
        projectName: [
          {
            required: true,
            message: "please type the project name",
            trigger: "blur",
          },
        ],
      },

      tableNames: [],
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          // this.ruleForm.tableName = data.data.tableName;
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.embed = data.data.embed;

          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          const arg = `micro rpc --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${this.ruleForm.tableName} --embed=${this.ruleForm.embed}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = {
        dsnAddr: this.ruleForm.dsnAddr,
        embed: true,
      };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
      };
      axios
        .post(apiUrl, formdata, { timeout: 5000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("Get table name success")
              : this.$message.error("Failed to get table name");
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          // console.log(err);
          this.$message.error("Failed to get table name");
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(other)";
        this.$forceUpdate();
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: small;
}
</style>



