<template>
  <el-row :gutter="20">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="module名称" prop="moduleName">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.moduleName"
              placeholder="请输入module名称"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i
                class="el-icon-question"
                style="font-size: 20px; vertical-align: middle; color: grey;"
              ></i>
              <div slot="content" style="padding: 5px;">
                例如已存在user服务，这里填写user服务目录下<br />
                go.mod文件里的module名称。
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="rpc服务名称" prop="rpcServerNames">
            <el-input
              style="width: 80%; font-size: small"
              v-model.trim="ruleForm.rpcServerNames"
              placeholder="多个服务用逗号分隔"
            ></el-input>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>
        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            >下载代码</el-button
          >
          <DownloadCode ref="downloadCode"></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "MicroRPCClient",
  data() {
    return {
      title: "生成rpc服务连接代码",
      path: "micro-rpc-cli",
      helpInfo: `
      <p>rpc服务连接代码包括常用服务治理功能，例如服务发现、负载均衡、限流、熔断、链路跟踪等。</p>
      `,
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,

      ruleForm: {
        moduleName: "",
        rpcServerNames: "",
      },
      rules: {
        moduleName: [
          { required: true, message: "请输入go module名称", trigger: "blur" },
        ],
        rpcServerNames: [
          { required: true, message: "请输入rpc服务名称", trigger: "blur" },
        ],
      },
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.rpcServerNames = data.data.rpcServerNames;
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          const arg = `micro rpc-cli --module-name=${this.ruleForm.moduleName} --rpc-server-name=${this.ruleForm.rpcServerNames}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    clearFormData() {
      this.ruleForm = {};
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 50px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}
</style>



